.p1 {
    font-size: 40px;
    color: #32325d;
    margin-top: 30px;
    margin-bottom: 80px;
    font-weight: bold;
}
.trans {
    -webkit-transform: skewY(8deg);
        -ms-transform: skewY(8deg);
            transform: skewY(8deg);
}

.pretext p {
    font-family: Camphor,Open Sans,Segoe UI,sans-serif;
    font-weight: 400;
    font-size: 17px;
    line-height: 28px;
    color: #525f7f;
}

.separation {
    margin-top: 40px;
}

h1 {
    color: #32325d;
}

.bubble {
    margin: 10px 0 10px 0;
    background: rgb(223, 223, 223);
    border-radius: 20px;
    padding: 10px;
    z-index: -1;
}
.space {
    margin-bottom: 100px;
}

.wave {
    margin-bottom: 100px;
    padding: 50px 0 10px 0;
    width: 100%;
    -webkit-transform: skewY(-8deg);
        -ms-transform: skewY(-8deg);
            transform: skewY(-8deg);
    z-index: -2;
    /*background: linear-gradient(100grad, #CADBEA 8.73828125%,#F6F9FC 97.85286458333333%);*/
    background: #E4ECF4;
}
.wave1 {
    padding: 50px 0 100px 0;
    width: 600px;
    background-image: -webkit-gradient(linear,left top, right top,from(hsla(0,0%,100%,0)),color-stop(97%, rgba(255, 255, 255, 0.959)));
    background-image: -webkit-linear-gradient(left,hsla(0,0%,100%,0),rgba(255, 255, 255, 0.959) 97%);
    background-image: -o-linear-gradient(left,hsla(0,0%,100%,0),rgba(255, 255, 255, 0.959) 97%);
    background-image: linear-gradient(90deg,hsla(0,0%,100%,0),rgba(255, 255, 255, 0.959) 97%); 
} 

.mates {
    position: absolute;
    padding: 50px 0 100px 0;
    width: 600px;
    left: calc(50% + 352px);
    -webkit-transform: skewY(-8deg);
        -ms-transform: skewY(-8deg);
            transform: skewY(-8deg);
    background: -webkit-gradient(linear,left top, right top,from(rgba(202, 219, 234, 0.507)),to(#E6EBF1));
    background: -webkit-linear-gradient(left,rgba(202, 219, 234, 0.507),#E6EBF1);
    background: -o-linear-gradient(left,rgba(202, 219, 234, 0.507),#E6EBF1);
    background: linear-gradient(100grad,rgba(202, 219, 234, 0.507),#E6EBF1);
} 

